import Vue from 'vue'
import Router from 'vue-router'
import Cover from '../views/Cover.vue'
import Home from '../views/Home.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component:Cover
    },
    {
      path:'/home',
      component:Home
    }
  ]
})



